.TodoItem {
    /* background-color: #fafafa; */
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0 5px 50px rgba(32, 35, 41, .15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    /* font-weight: 400; */
    font-family: cursive;
}

.TodoItem-p--complete {
    text-decoration: line-through
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    font-size: 24px;
    font-weight: 700
}

.Icon-check {
    position: absolute;
    left: 12px;
    color: gray;
    /* opacity: .5; */
}

.Icon-check--active {
    color: #4caf50;
    opacity: initial;
}

.Icon-delete {
    position: absolute;
    top: -20px;
    right: 0;
    color: gray;
    /* opacity: 0.5; */
}

.Icon-delete:hover {
    color: red;
    opacity: initial;
}